<template>
    <div class="page">
        <!-- <div class="logOut">
            用户名: {{userName}}
            <span @click="logOut">退出</span>
        </div> -->
        <nav>
            <ul>
                <li @click="search()">查询用户</li>
                <li @click="addfeel">添加文章</li>
                <li @click="delUser">删除用户</li>
            </ul>
        </nav>
        <table class="userList" border="0" cellspacing="0">
            <tr>
                <th>用户名</th>
                <th>姓名</th>
                <th>年龄</th>
                <th>性别</th>
                <th>地址</th>
                <th>联系方式</th>
            </tr>
            <tr v-for="(item,index) in items" keys="index">
                <td>{{item.name}}</td>
                <td>{{item.userName}}</td>
                <td>{{item.age}}</td>
                <td>{{item.sex}}</td>
                <td>{{item.address}}</td>
                <td>{{item.phone}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
import ajax from "../../libs/helpers/ApiHelper";
export default {
  data() {
    return {
      userName: "",
      items: []
    };
  },
  methods: {
    search() {
      ajax.search({}).then(res => {
        if (res.returnCode === "000000") {
          this.items = res.data;
          console.log(this.items);
        }
      });
    },
    addfeel() {
      this.$router.push("/addFeel");
    },
    delUser() {},
    logOut() {
      ajax.logOut({}).then(res => {
        if (res.returnCode === "000000") {
          this.Storage.sessionSet('LOGINSHOW',false)
          this.$router.push("/logins");
        }
      });
    }
  },
  mounted() {
      console.log(this.$route)
      this.userName = this.$route.query.userName;
  }
};
</script>




